.background {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 15px;
  background: #9053c7;
  background: linear-gradient(-135deg, #c850c0, #4158d0);
}
.background2 {
  align-items: start;
}

.btn_exit
{
  position:fixed;
  top:10px;
  right:20px;
  font-family: Montserrat-Bold;
  font-size: 15px;
  line-height: 1.5;
  color: #fff;
  text-transform: uppercase;

  width: 100px;
  height: 40px;
  border-radius: 25px;
  background: #57b846;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25px;


  transition: all 0.4s;
  border:none;
  cursor:pointer;
  outline:none;
}

.btn_exit:hover {
  background: #333333;
}
.btn_exit:active {
  outline:none;
}
